*{
    margin: 0 auto;
}

.conteudo-centralizado{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 25px;

    border: 1px solid white;
    border-radius: 15px;
    box-shadow: 0px 0px 5px 0px;
    margin-top: 5vh;
    padding: 20px;
    background-color: #f8fafb;

    max-width: 425px;
    width: 80%;

}

.area-botao{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 15px;
}

button{
    padding: 10px 25px;
    background-color: #2ee5df;
    border: none;
    border-radius: 5px;
    color: white;
}

button:hover{
    background-color: #2abbb7;
}